import {
  Directive,
  ElementRef,
  Input,
  OnInit,
  OnChanges,
  OnDestroy,
  Renderer,
  SimpleChanges,
  Self,
  Optional,
  Inject,
  forwardRef
} from '@angular/core';

import {Subscription} from 'rxjs/Subscription';

import {BaseFxDirective} from './base';
import {MediaChange} from '../../media-query/media-change';
import {MediaMonitor} from '../../media-query/media-monitor';

import {HideDirective} from "./hide";
import {LayoutDirective} from './layout';



const FALSY = ['false', false, 0];

/**
 * 'show' Layout API directive
 *
 */

export class ShowDirective extends BaseFxDirective implements OnInit, OnChanges, OnDestroy {
  /**
   * Original dom Elements CSS display style
   */
  private _display = 'flex';

  /**
    * Subscription to the parent flex container's layout changes.
    * Stored so we can unsubscribe when this directive is destroyed.
    */
  private _layoutWatcher : Subscription;

         set show(val)     { this._cacheInput("show", val); }
      set showXs(val)   { this._cacheInput('showXs', val); }
   set showGtXs(val) { this._cacheInput('showGtXs', val); };
      set showSm(val)   { this._cacheInput('showSm', val); };
   set showGtSm(val) { this._cacheInput('showGtSm', val); };
      set showMd(val)   { this._cacheInput('showMd', val); };
   set showGtMd(val) { this._cacheInput('showGtMd', val); };
      set showLg(val)   { this._cacheInput('showLg', val); };
   set showGtLg(val) { this._cacheInput('showGtLg', val); };
      set showXl(val)   { this._cacheInput('showXl', val); };
  /**
   *
   */
  constructor(
      monitor : MediaMonitor,
        private _layout: LayoutDirective,
         private _hideDirective,
      protected elRef: ElementRef,
      protected renderer: Renderer)
  {
    super(monitor, elRef, renderer);

    if (_layout) {
      /**
       * The Layout can set the display:flex (and incorrectly affect the Hide/Show directives.
       * Whenever Layout [on the same element] resets its CSS, then update the Hide/Show CSS
       */
      this._layoutWatcher = _layout.layout$.subscribe(() => this._updateWithValue());
    }
  }

  /**
    * Does the current element also use the fxShow API ?
    */
   protected get usesHideAPI() {
     return !!this._hideDirective;
   }


  // *********************************************
  // Lifecycle Methods
  // *********************************************

  /**
   * On changes to any @Input properties...
   * Default to use the non-responsive Input value ('fxShow')
   * Then conditionally override with the mq-activated Input's current value
   */
  ngOnChanges(changes: SimpleChanges) {
    if (changes['show'] != null || this._mqActivation) {
      this._updateWithValue();
    }
  }

  /**
   * After the initial onChanges, build an mqActivation object that bridges
   * mql change events to onMediaQueryChange handlers
   */
  ngOnInit() {
    this._listenForMediaQueryChanges('show', true, (changes: MediaChange) =>{
      this._updateWithValue(changes.value);
    });
    this._updateWithValue();
  }

  ngOnDestroy() {
     super.ngOnDestroy();
     if (this._layoutWatcher) {
       this._layoutWatcher.unsubscribe();
     }
   }

  // *********************************************
  // Protected methods
  // *********************************************

  /** Validate the visibility value and then update the host's inline display style */
  private _updateWithValue(value?: string|number|boolean) {
    value = value || this._queryInput("show") || true;
    if (this._mqActivation) {
      value = this._mqActivation.activatedInput;
    }

    let shouldShow = this._validateTruthy(value);
    if ( shouldShow || !this.usesHideAPI ) {
      this._applyStyleToElement(this._buildCSS(shouldShow));
    }
  }


  /** Build the CSS that should be assigned to the element instance */
  private _buildCSS(show) {
    return {'display': show ? this._display : 'none'};
  }

  /**  Validate the to be not FALSY */
  _validateTruthy(show) {
    return (FALSY.indexOf(show) == -1)
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{selector: `
  [fxShow],
  [fxShow.xs]
  [fxShow.gt-xs],
  [fxShow.sm],
  [fxShow.gt-sm]
  [fxShow.md],
  [fxShow.gt-md]
  [fxShow.lg],
  [fxShow.gt-lg],
  [fxShow.xl]
`}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: MediaMonitor, },
{type: LayoutDirective, decorators: [{ type: Optional }, { type: Self }, ]},
{type: undefined, decorators: [{ type: Inject, args: [forwardRef(() => HideDirective), ] }, { type: Optional }, { type: Self }, ]},
{type: ElementRef, },
{type: Renderer, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'show': [{ type: Input, args: ['fxShow', ] },],
'showXs': [{ type: Input, args: ['fxShow.xs', ] },],
'showGtXs': [{ type: Input, args: ['fxShow.gt-xs', ] },],
'showSm': [{ type: Input, args: ['fxShow.sm', ] },],
'showGtSm': [{ type: Input, args: ['fxShow.gt-sm', ] },],
'showMd': [{ type: Input, args: ['fxShow.md', ] },],
'showGtMd': [{ type: Input, args: ['fxShow.gt-md', ] },],
'showLg': [{ type: Input, args: ['fxShow.lg', ] },],
'showGtLg': [{ type: Input, args: ['fxShow.gt-lg', ] },],
'showXl': [{ type: Input, args: ['fxShow.xl', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
